.dPageHeader {
  // background-color: red;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  max-width: 42rem;
}

.dPageBody {
  // background-color: yellow;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  max-width: 42rem;
  // margin: 15px;
}

.dPageFooter {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  max-width: 42rem;
  // background-color: green;
}

.footerMainContainer {
  width: 60%;
  // border: green 1px solid;
  display: flex;
  margin-bottom: 25%;
}

.footerLeftContainer {
  // border: yellow 1px solid;
  flex: 0.1;
  padding-right: 20px;
  padding-top: 15px;
}

.footerRightContainer {
  flex: 0.9;
  display: flex;
  flex-direction: column;
}

.footerTopTextStyle {
  font-family: 'Merriweather', 'Georgia', serif;
}

.link {
  color: #d23669;
  box-shadow: 0 1px 0 0;
}

.link:hover {
  color: #d23669;
  box-shadow: 0 0 0 0;
}

.mainContainer {
  width: 100%;
  flex: 0.85;
  margin-bottom: 5%;
  // border: orange 1px solid;
  display: flex;
  flex-direction: column;
  cursor: pointer;

  .titleContainer {
    // border: green 1px solid;
    width: 100%;
  }

  .dateContainer {
    // border: blue 1px solid;
    width: 100%;
    font-family: 'Merriweather', 'Georgia', serif;
  }

  .bottomHalfContainer {
    // border: pink 1px solid;
    > p {
      color: black;
      font-family: 'Merriweather', 'Georgia', serif;
      font-weight: 400;
    }
  }
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */

  .dPageHeader {
    // background-color: red;
    display: flex;
    flex-direction: column;
    margin-right: auto;
    margin-right: auto;
    max-width: 42rem;
    padding-left: 15px;
  }

  .content {
    margin: 15px;
  }

  .dPageFooter {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    max-width: 42rem;
    padding-left: 15px;
    // background-color: red;
  }

  .footerMainContainer {
    width: 100%;
  }

  .mainContainer {
    width: 100%;
    flex: 0.85;
    margin-bottom: 5%;
    // border: orange 1px solid;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    padding: 5px;

    .titleContainer {
      // border: green 1px solid;
      width: 100%;
    }

    .dateContainer {
      // border: blue 1px solid;
      width: 100%;
      font-family: 'Merriweather', 'Georgia', serif;
      font-size: 14px;
    }

    .bottomHalfContainer {
      // border: pink 1px solid;
      > p {
        color: black;
        font-family: 'Merriweather', 'Georgia', serif;
        font-weight: 400;
        font-size: 14px;
      }
    }
  }
}

@media only screen and (max-width: 1280px) {
  .dPageHeader {
    // background-color: red;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    max-width: 42rem;
  }

  .mainContainer {
    width: 100%;
    flex: 0.85;
    margin-bottom: 5%;
    // border: orange 1px solid;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    margin: 10px;

    .titleContainer {
      // border: green 1px solid;
      width: 100%;
    }

    .dateContainer {
      // border: blue 1px solid;
      width: 100%;
      font-family: 'Merriweather', 'Georgia', serif;
      font-size: 14px;
    }

    .bottomHalfContainer {
      // border: pink 1px solid;
      > p {
        color: black;
        font-family: 'Merriweather', 'Georgia', serif;
        font-weight: 400;
        font-size: 14px;
      }
    }
  }
}
